@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ant-row {
 margin-right: 0 !important;
 margin-left: 0 !important;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.map-container {
  height: 400px;
  border-radius: 4px;
  overflow: hidden;
}

.map-container iframe {
  width: 100%;
  height: 100%;
  border: 0;
}

.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

form {
  width: 100%;
  padding: 0 40px !important;
}

label {
  color: white !important;
  font-weight: 600 !important;
}

.pages-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.pages-list > * {
  margin-bottom: 4px;
}

.info-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.info-list > * {
  margin-bottom: 4px;
}

.info-list-item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.info-list span {
  margin-right: 10px;
}
.section-container {
  width: 100%;
  box-shadow: 6px 4px 5px 0px black;
}

.title-container h1 {
  font-size: 40px;
  margin: 26px 0 50px 0;
  font-weight: 700;
}
.whatsapp-container {
  position: fixed;
  bottom: 12px;
  right: 12px;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  background: #25d366;
  box-shadow: -1px 0px 6px black;
  cursor: pointer;
}
.media-container > * {
  margin-right: 16px;
}
.header-content {
  max-width: 1400px;
  margin: 0 auto;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  float: left;
  width: 100px;
  height: 64px;
  margin: 0px 24px 0px 0;
  font-weight: bold;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(/static/media/logo_hd_small.e19aaa13.png);
}

.slide-image {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  max-height: 90vh;
}

.slide-image.slide-1 {
  background-image: url(/static/media/bg1.da960bb0.jpg);
}

.slide-image.slide-2 {
  background-image: url(/static/media/bg2.f82146af.jpg);
}

.slide-image.slide-3 {
  background-image: url(/static/media/bg3.e64e4691.png);
}

.slide-image.slide-4 {
  background-image: url(/static/media/bg4.5abdd508.png);
}

.twitter-container {
  width: 100%;
  padding: 0 5%;
}

.twitter-container-small {
  width: 25%;
  display: block;
  position: absolute;
  top: 40px;
  right: 20px;
}
.banner-overlay {
  font-size: 40px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #000000a6;
  color: #eff2f5;
  font-size: 60px;
  text-shadow: 3px 3px black;
}
.text-container {
  padding: 0 10%;
}

.text-container h1, h2 {
  line-height: 60px;
  text-align: start;
}

.text-container h1 {
  font-size: 60px;
  margin: 30px 0px 5px 0px;
}

@media (max-width: 450px) {
  .text-container h1 {
    font-size: 30px;
  }
}
